.App {
  display: flex;
  height: 100vh;
}

.App > div {
  width: 50%;
  height: auto;
  height: 100%;
}


.App > div:last-child {
  flex: 1;
  height: auto;
  height: 100%;
  overflow: auto;
}

.ReactCodeMirror > .CodeMirror {
  border: 1px solid #eee;
  height: 100vh;
  background-color: #f6f6f0;
}